div {
	margin-top: 6em;
	display: grid;
	place-items: center;
}

html,
body {
	padding: 0;
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans,
		Helvetica Neue, sans-serif;
	line-height: 1.6;
	font-size: 28px;
}

* {
	box-sizing: border-box;
}

a {
	color: #e111ee;
	text-decoration: none;
}

a:hover {
	text-decoration: underline;
}

img {
	max-width: 100%;
	display: block;
}
